"use client";

import { useState, useRef, useCallback } from "react";
import { RealtimeAgent, RealtimeSession } from "@openai/agents/realtime";

export default function VoiceAgent() {
    const [isConnected, setIsConnected] = useState(false);
    const [isConnecting, setIsConnecting] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const [isSpeaking, setIsSpeaking] = useState(false);
    const sessionRef = useRef<RealtimeSession | null>(null);

    const createSession = useCallback(async () => {
        try {
            setIsConnecting(true);
            setError(null);

            // Get ephemeral token from your API route
            const tokenResponse = await fetch("/api/realtime-token", {
                method: "POST",
            });

            if (!tokenResponse.ok) {
                console.error(tokenResponse);
                throw new Error("Failed to get client token");
            }

            const { clientSecret } = await tokenResponse.json();

            // Create the voice agent
            const agent = new RealtimeAgent({
                name: "Assistant",
                instructions:
                    "You are a helpful voice assistant. Speak naturally and conversationally. Keep responses concise and engaging.",
            });

            // Create and connect the session
            const session = new RealtimeSession(agent, {
                model: "gpt-realtime",
            });

            // Set up event listeners
            session.on("connected", () => {
                console.log("Session connected");
                setIsConnected(true);
                setIsConnecting(false);
            });

            session.on("disconnected", () => {
                console.log("Session disconnected");
                setIsConnected(false);
                setIsConnecting(false);
            });

            session.on("error", (error) => {
                console.error("Session error:", error);
                setError(error.message);
                setIsConnecting(false);
            });

            // Listen for when the agent starts/stops speaking
            session.on("agent.speaking.started", () => {
                setIsSpeaking(true);
            });

            session.on("agent.speaking.stopped", () => {
                setIsSpeaking(false);
            });

            // Connect to the session
            await session.connect({
                apiKey: clientSecret,
            });

            sessionRef.current = session;
        } catch (err) {
            console.error("Failed to create session:", err);
            setError(err instanceof Error ? err.message : "Failed to connect");
            setIsConnecting(false);
        }
    }, []);

    const disconnect = useCallback(async () => {
        if (sessionRef.current) {
            await sessionRef.current.disconnect();
            sessionRef.current = null;
        }
        setIsConnected(false);
        setIsSpeaking(false);
    }, []);

    const handleToggleConnection = useCallback(async () => {
        if (isConnected) {
            await disconnect();
        } else {
            await createSession();
        }
    }, [isConnected, createSession, disconnect]);

    return (
        <div className="flex flex-col items-center justify-center min-h-screen bg-gray-100 p-8">
            <div className="bg-white rounded-2xl shadow-lg p-8 max-w-md w-full">
                <h1 className="text-3xl font-bold text-center mb-8 text-gray-800">
                    Voice Agent
                </h1>

                {error && (
                    <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
                        {error}
                    </div>
                )}

                <div className="text-center mb-8">
                    <div
                        className={`mx-auto w-24 h-24 rounded-full flex items-center justify-center mb-4 transition-all duration-300 ${
                            isSpeaking
                                ? "bg-green-500 animate-pulse"
                                : isConnected
                                  ? "bg-blue-500"
                                  : "bg-gray-300"
                        }`}
                    >
                        <svg
                            className="w-10 h-10 text-white"
                            fill="currentColor"
                            viewBox="0 0 20 20"
                        >
                            <path
                                fillRule="evenodd"
                                d="M7 4a3 3 0 016 0v4a3 3 0 11-6 0V4zm4 10.93A7.001 7.001 0 0017 8a1 1 0 10-2 0A5 5 0 015 8a1 1 0 00-2 0 7.001 7.001 0 006 6.93V17H6a1 1 0 100 2h8a1 1 0 100-2h-3v-2.07z"
                                clipRule="evenodd"
                            />
                        </svg>
                    </div>

                    <p className="text-sm text-gray-600 mb-2">
                        {isConnecting
                            ? "Connecting..."
                            : isConnected
                              ? isSpeaking
                                  ? "Assistant is speaking..."
                                  : "Listening... Start talking!"
                              : "Ready to connect"}
                    </p>

                    <p className="text-xs text-gray-500">
                        {isConnected
                            ? "You can interrupt the assistant anytime"
                            : "Grant microphone permission when prompted"}
                    </p>
                </div>

                <button
                    onClick={handleToggleConnection}
                    disabled={isConnecting}
                    className={`w-full py-3 px-6 rounded-lg font-semibold transition-all duration-200 ${
                        isConnected
                            ? "bg-red-500 hover:bg-red-600 text-white"
                            : "bg-blue-500 hover:bg-blue-600 text-white disabled:bg-gray-400 disabled:cursor-not-allowed"
                    }`}
                >
                    {isConnecting
                        ? "Connecting..."
                        : isConnected
                          ? "Disconnect"
                          : "Start Voice Chat"}
                </button>

                {isConnected && (
                    <div className="mt-4 text-xs text-gray-500 text-center">
                        <p>• Speak naturally to the assistant</p>
                        <p>• The assistant will respond with voice</p>
                        <p>• You can interrupt at any time</p>
                    </div>
                )}
            </div>
        </div>
    );
}
